function Navbar() {
    let color = "beige"
    function onPress (e) {
        e.preventDefault();
        const target = window.document.getElementById(
          e.currentTarget.href.split('#')[1]
        );
        if (target) {
          target.scrollIntoView({ behavior: "smooth" });
        }
      };
    return (
    <div className="testing"
        style={{
            position: "fixed",
            backgroundColor: color,
            width: "100%",
            zIndex: "9999"
        }}
    >
        <div
            style={{
                display: "flex",
                justifyContent: "center",
            }}
        >
          <a onClick={(e) => onPress(e)} className={"nav-item"} href="#home">
            <div data-to-scrollspy-id="home" className={"ss-item"}>
              Home
            </div>
          </a>
          <a onClick={(e) => onPress(e)} className={"nav-item"} href="#aboutme">
            <div data-to-scrollspy-id="aboutme" className={"ss-item"}>
              About Me
            </div>
          </a>
          <a onClick={(e) => onPress(e)} className={"nav-item"} href="#traveling">
            <div data-to-scrollspy-id="traveling" className={"ss-item"}>
              Traveling
            </div>
          </a>
          <a onClick={(e) => onPress(e)} className={"nav-item"} href="#walking">
            <div data-to-scrollspy-id="walking" className={"ss-item"}>
              My Walks
            </div>
          </a>
          <a onClick={(e) => onPress(e)} className={"nav-item"} href="#entertainment">
            <div data-to-scrollspy-id="entertainment" className={"ss-item"}>
              Entertainment
            </div>
          </a>

        </div>
    </div>
    );
  }
  
  export default Navbar;
  